* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  font-family: sans-serif;
  padding: 0;
  margin: 0;
  font-size: 1rem;
  line-height: 1.5rem;
}

nav {
  margin: 1rem 5%;
}
nav ul {
  margin: 0;
  padding: 0;
}
nav li {
  display: inline;
  margin: 0 1rem;
}
nav li:first-child {
  margin-left: 0;
}
@media screen and (min-width: 670px) {
  nav {
    margin-left: 1rem;
  }
}

main {
  width: 90%;
  max-width: 720px;
  margin: 3rem auto;
}

h1, h2, h3, h4, strong, em, p code, li code,
h1 span, h2 span, h3 span, h4 span {
  background: linear-gradient(90deg, #9e009e 0%, #ff007f 49%, #d98400 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

h1, h2, h3, h4 {
  font-family: sans-serif;
  font-weight: 400;
}

h1 {
  margin: 2rem 0 1.5rem;
  font-size: 2.5rem;
  line-height: 3rem;
  font-weight: 300;
  display: table;
}

h2 {
  margin: 2rem 0 1rem;
  font-size: 2rem;
  line-height: 2.5rem;
  font-weight: 400;
}

h3 {
  margin: 1rem 0 0.5rem;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 500;
}

h4 {
  margin: 0.5rem 0;
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 500;
}

hgroup {
  margin: 1rem 0;
}
hgroup h1, hgroup h2, hgroup h3, hgroup h4 {
  margin: 0;
}

p, ul, ol, iframe, blockquote {
  margin: 1rem 0;
  font-size: 1rem;
  line-height: 1.5rem;
}
p code, ul code, ol code, iframe code, blockquote code {
  line-height: 0.5rem;
}

ul, ol {
  padding: 0 0 0 1.5rem;
}

footer img {
  height: 24px;
}

section {
  padding: 1rem 0;
}

hr {
  margin: 2rem 0;
}

sup.footnote-ref {
  font-size: 1rem;
  line-height: 0.5rem;
}

blockquote {
  border-left: 6px rgba(1, 1, 1, 0.05) solid;
  background-color: rgba(1, 1, 1, 0.05);
  padding-left: 1rem;
  padding-right: 0.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

dt {
  margin-top: 1rem;
}

dd {
  margin-bottom: 1rem;
  margin-left: 1rem;
}